import React from "react";
import { Card, CardBody, CardHeader, CardTitle, Table } from "react-bootstrap"
import ScreenoverviewSkeleton from "../../layout/skeleton/ScreenoverviewSkeleton";

const ScreensOverview=(props)=>{

     const { data, loader } = props
    return(
      <>
        <Card className="sw-card-custom gutter-b card-shadow">
          <CardHeader className="px-5 pt-5 border-0 pb-0">
            <CardTitle className="m-0">
              <h6 class="card-label font-weight-bolder m-0">Screens Overview</h6>
            </CardTitle>
          </CardHeader>
          <CardBody className="py-5 px-0">
            <Table responsive>
              <thead>
                <tr>
                  <th className="text-muted" style={{ width: "20%" }}>
                    Screen Name
                  </th>
                  <th className="text-muted" style={{ width: "20%" }}>
                    Screen IP
                  </th>
                  <th className="text-muted" style={{ width: "20%" }}>
                    Device Location
                  </th>
                  <th className="text-muted" style={{ width: "10%" }}>
                    Status
                  </th>
                  <th className="text-muted" style={{ width: "20%" }}>
                    Last Refresh Time
                  </th>
                  <th className="text-muted" style={{ width: "10%" }}>
                    Daily Active Hours
                  </th>
                </tr>
              </thead>
              <tbody>


               { loader ? <ScreenoverviewSkeleton/> : data &&  Object.keys(data).length ? data.map((e)=>{  

                return(<tr className="border-top">
                  <td>
                      <strong>{e.screenName}</strong>
                    </td>
                    <td><div className="">{e.screenIp}</div></td>
                    <td>
                      {e.deviceLocation}
                    </td>
                    <td>
                      <label className={`mx-width-80 alert ${e.status=="active" ? 'alert-success' : 'alert-danger'} m-0 rounded-0 px-3 py-1`}>
                        {e.status}
                      </label>
                    </td>
                    <td>
                       {e.lastRefreshTime}
                    </td>
                    <td>
                     {e.activeHours}
                    </td>
                  </tr>
                  )
                })
                 : <tr>not found</tr>
              }
                </tbody>
            </Table> 
          </CardBody>
        </Card>
      </>
    )
}   

export default React.memo(ScreensOverview);